import React from "react"
import { makeStyles } from "@material-ui/core/styles"
import { Card,
	CardActionArea,
	CardActions,
	CardContent,
	CardMedia,
	Typography
} from "@material-ui/core"
import DeleteIcon from "@material-ui/icons/Delete"
import EditIcon from "@material-ui/icons/Edit"
import LinkIcon from '@material-ui/icons/Link'
import LaunchIcon from '@material-ui/icons/Launch'
import MoreVertIcon from '@material-ui/icons/MoreVert';
import IconButton from "@material-ui/core/IconButton"
import firebase from "../firebase"

import { CopyToClipboard } from 'react-copy-to-clipboard'

const useStyles = makeStyles(theme => ({
	cardContainer: {
		background: theme.palette.background.paper,
		width: "100%",
		borderColor: theme.palette.primary.light,
		borderRadius: theme.spacing(1)
	},
	cardContent: {
		lineHeight: 1.25,
		textOverflow: "ellipsis"
	}
}))

const LinkCard = props => {

	const classes = useStyles()
	let hostName = getHostName(props.url)
	const linkDetails = {
		link_title: props.title,
		link_url: props.url,
		folder_id: props.folder,
		link_img: props.img,
		date_added: props.date_added
	}

	const handleDeleteLinkCard = (id, updateDelete) => {
		firebase.firestore()
		.collection("links")
		.doc(id)
		.delete()
		.then(() => {
			updateDelete("Link successfully deleted", linkDetails)
			console.log("Success")
		}).catch((err) => {
			updateDelete("Error deleting link", linkDetails)
			console.error("Error removing document: ", err)
		}) 
	}

	return (

			<Card variant="outlined" className={classes.cardContainer} >
				<CardActionArea>
					{props.img !== "" 
						? (
							<CardMedia
								component="img"
								alt={props.title}
								image={props.img}
								title={props.title}
							/> 
						) : ( "" )
					}
					<CardContent>
						<Typography variant="overline">{hostName}</Typography>
						<Typography className={classes.cardContent} variant="subtitle1" gutterBottom>{props.title}</Typography>
						<Typography noWrap className={classes.cardContent} variant="caption" gutterBottom>{props.url}</Typography>
					</CardContent>
				</CardActionArea>
				<CardActions>
					<IconButton size="small" color="primary"
						onClick={() => props.onClickEdit({docId: props.contentId, ...linkDetails})}>
						<EditIcon />
					</IconButton>
					<IconButton size="small" color="primary"
						onClick={() => handleDeleteLinkCard(props.contentId, props.onClickDelete)}
					>
						<DeleteIcon />
					</IconButton>
					<CopyToClipboard text={props.url} onCopy={() => props.onLinkCopy("Link URL copied to clipboard")}>
						<IconButton size="small" color="primary">
							<LinkIcon />
						</IconButton>
					</CopyToClipboard>
					<IconButton size="small" color="primary"
						onClick={e => {
							e.preventDefault()
							window.open(props.url, "_blank")
						}}
					>
						
						<LaunchIcon />
					</IconButton>
					<IconButton size="small" color="primary" disabled
						onClick={() => props.onClickEdit({docId: props.contentId, ...linkDetails})}>
						<MoreVertIcon />
					</IconButton>
				</CardActions>
			</Card>

		
	)
}

function getHostName(url) {
	const matches = url.match(/^https?:\/\/(www.)?([^/?#]+)(?:[/?#]|$)/i)
	return matches && matches[2]
}

export default LinkCard