import React, { useState } from "react"
import CssBaseline from "@material-ui/core/CssBaseline"
import BottomNav from "./components/BottomNav"
import Home from "./pages/Home"
import Collection from "./pages/Collection"
import Favorites from "./pages/Favorites"
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom'
import { ThemeProvider } from '@material-ui/styles'
import { lightTheme, darkTheme } from "./services/Themes.js"

function App() {

	const [tab, setTab] = useState("")
	const [defaultTheme, setDefaultTheme] = useState(false)

	const handleToggleTheme = () => {
		setDefaultTheme(!defaultTheme)
	}

	return (
		<ThemeProvider theme={defaultTheme ? darkTheme : lightTheme} >
			<CssBaseline />
			<Router basename="/link-library">
				<Switch>
					<Route exact path="/">
						<Home onToggleTheme={handleToggleTheme} />
					</Route>
					<Route path="/favorites">
						<Favorites onToggleTheme={handleToggleTheme} />
					</Route>
					<Route path="/collection">
						<Collection onToggleTheme={handleToggleTheme} />
					</Route>
					<Route path="/settings">
						<Favorites onToggleTheme={handleToggleTheme} />
					</Route>
				</Switch>
				<BottomNav
					value={tab}
					onChange={setTab}
				/>
			</Router>
		</ThemeProvider>
	)
}

export default App;