import React, { useState, useEffect } from 'react'
import { 
  makeStyles,
  List, 
  ListItem,
  ListItemAvatar,
  ListItemText,
  Avatar,
  IconButton,
  Grid,
  Dialog,
  AppBar,
  Toolbar,
  Slide,
  Paper,
  Typography,
  Snackbar,
  Button
} from "@material-ui/core"

import FolderIcon from '@material-ui/icons/Folder'
import DeleteIcon from '@material-ui/icons/Delete'
import SearchIcon from '@material-ui/icons/Search'
import MoreIcon from '@material-ui/icons/MoreVert'
import ArrowBackIcon from '@material-ui/icons/ArrowBack'

import StackGrid from "react-stack-grid"
import firebase from "../firebase"
import Folder from './Folder'
import Header from '../components/Header'
import FormDialog from '../components/FormDialog'
import CloseIcon from '@material-ui/icons/Close'
import EditIcon from '@material-ui/icons/Edit'

import { SwipeableListItem, ActionAnimations } from '@sandstreamdev/react-swipeable-list'
import '@sandstreamdev/react-swipeable-list/dist/styles.css'
import EditFormDialog from '../components/EditFormDialog'


const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
		width: `calc(100vw - ${theme.spacing(4)}px)`,
    margin: theme.spacing(9, 2, 18, 2),
    zIndex: 0
  },
  title: {
    margin: theme.spacing(4, 0, 2),
  },
  bottomAppBar: {
    top: "auto",
    bottom: 0
  },
  avatar: {
    backgroundColor: theme.palette.secondary.main
  },
  folderIcon: {
    color: theme.palette.primary.main
  },
  swipeRightContainer: {
    display: "flex",
    alignItems: "center",
    height: "100%",
    width: "100%",
    background: "#ef5350",
    justifyContent: "flex-start",
  },
  swipeRightAction: {
    display:"flex",
    alignItems: "center",
    color: "#fff",
    alignContent: "flex-start"
  },
  swipeIcon: {
    margin: theme.spacing(0, 1)
  },
  swipeLeftContainer: {
    display: "flex",
    alignItems: "center",
    height: "100%",
    width: "100%",
    background: "#1976d2",
    justifyContent: "flex-end",
  },
  swipeLeftAction: {
    display:"flex",
    alignItems: "center",
    color: "#fff",
    alignContent: "flex-end",
  },
  snackBar: {
		position: "fixed",
		bottom: theme.spacing(18)
  },
  listItem: {
    background: theme.palette.background.paper
  }
}))

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="left" ref={ref} {...props} />
});

const Collection = props => {

  const [open, setOpen] = useState(false)
  const [openSnackbar, setOpenSnackbar] = useState(false)
  const [snackbarMessage, setSnackbarMessage] = useState("")
  const [snackbarUndoButton, setSnackbarUndoButton] = useState(false)
  const [folderId, setFolderId] = useState("")
  const [folderName, setFolderName] = useState("")
  const [folderDesc, setFolderDesc] = useState("")
  const [showForm, setShowForm] = useState(false)
  const classes = useStyles()
  const folderData = useDetails()

  const handleClickOpen = () => {
    setOpen(true)
  }

  const handleClose = (event, reason) => {
    if (reason === 'clickaway') {
      return
    }
    setOpen(false)
    setOpenSnackbar(false)
  }

  const handleCloseSnackbar = () => {
    setOpenSnackbar(false)
  }

  const handleUndoDelete = () => {

    setOpenSnackbar(false)
    setSnackbarUndoButton(false)

    firebase.firestore()
      .collection("collections")
      .doc(folderId)
      .set({
        folder_name: folderName,
        folder_desc: folderDesc
      }).then(() => {
        console.log("Collection restored")
        setTimeout(() => {
          setOpenSnackbar(true)
          setSnackbarMessage("Collection restored")
        }, 300)
      }).catch(err => {
        console.error("Error restoring collection: ", err)
      })
  }

  const handleLinkClick = (id, name) => {
    setFolderId(id)
    setFolderName(name)
    handleClickOpen()
  }

  const handleDeleteCollection = (id, name, desc) => {
    setFolderId(id)
    setFolderName(name)
    setFolderDesc(desc)
    deleteCollection(id)

    setSnackbarUndoButton(true)
    setShowForm(false)
    setSnackbarMessage("Collection successfully deleted")
    setOpenSnackbar(true)
  }

  const handleAddCollection = message => {
    setSnackbarUndoButton(false)
    setSnackbarMessage(message)
    console.log({message})
    setOpenSnackbar(true)
  }

  const handleEditCollection = (id, name, desc) => {
    setFolderId(id)
    setFolderName(name)
    setFolderDesc(desc)
    setShowForm(true)
  }

  const handleUpdateCollection = message => {
    setSnackbarMessage(message)
    console.log({message})
    setOpenSnackbar(true)
    setShowForm(false)
  }

  const collections = folderData.map(folder =>
    <Grid item key={folder.folder_id}>
      <SwipeableListItem
        swipeLeft={{
          content:
            <div className={classes.swipeLeftContainer}>
              <div className={classes.swipeLeftAction}>
                <Typography variant="subtitle2">Edit</Typography>
                <EditIcon fontSize="large" className={classes.swipeIcon}/>                
              </div>
            </div>,
          action: () => handleEditCollection(folder.folder_id, folder.folder_name, folder.folder_desc),
          actionAnimation: ActionAnimations.RETURN
        }}
        swipeRight={{
          content: 
            <div className={classes.swipeRightContainer}>
              <div className={classes.swipeRightAction}>
                <DeleteIcon fontSize="large" className={classes.swipeIcon}/>
                <Typography variant="subtitle2">Delete</Typography>
                
              </div>
            </div>,
          action: () => handleDeleteCollection(folder.folder_id, folder.folder_name, folder.folder_desc),
          actionAnimation: ActionAnimations.REMOVE
        }}
        blockSwipe={folder.editable !== undefined ? true : false}
      >
        <ListItem className={classes.listItem} onClick={() => handleLinkClick(folder.folder_id, folder.folder_name)}>
          <ListItemAvatar>
            <Avatar className={classes.avatar}>
              <FolderIcon className={classes.folderIcon} />
            </Avatar>
          </ListItemAvatar>
          <ListItemText
            primary={<Typography variant="subtitle1">{folder.folder_name}</Typography>}
            secondary={folder.folder_desc}
          />
        </ListItem>
      </SwipeableListItem>
    </Grid>
  )

  return (
    <>
      <Header title={"Collection"} onToggleTheme={props.onToggleTheme} />
      <div className={classes.root}>
        <Paper variant="outlined" style={{borderRadius: 8}}>
        <List>
          <StackGrid 
            columnWidth="100%"
            gutterWidth={8}
            gutterHeight={0}
          >
            {collections}
          </StackGrid>
        </List>
        </Paper>
        <Dialog fullScreen open={open} TransitionComponent={Transition}>
          <Folder id={folderId} name={folderName} />
          <AppBar position="fixed" color="secondary" className={classes.bottomAppBar}>
            <Toolbar>
              <IconButton edge="start" color="inherit" aria-label="open drawer"
                onClick={handleClose}
              >
                <ArrowBackIcon />
              </IconButton>
              <IconButton color="inherit">
                <SearchIcon />
              </IconButton>
              <IconButton edge="end" color="inherit">
                <MoreIcon />
              </IconButton>
            </Toolbar>
          </AppBar>
        </Dialog>
      </div>
      <Snackbar
        className={classes.snackBar}
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'left',
        }}
        open={openSnackbar}
        onClose={handleClose}
        autoHideDuration={5000}
        message={snackbarMessage}
        action={
          <>
            {snackbarUndoButton ? (
              <Button color="secondary" aria-label="undo-delete" onClick={handleUndoDelete}>
                Undo
              </Button>
            ) : ( "" )}
            <IconButton size="small" aria-label="close" color="inherit" onClick={handleCloseSnackbar}>
              <CloseIcon fontSize="small" />
            </IconButton>
          </>
        }
      />
      <FormDialog mode={"collection"} onAdd={handleAddCollection} />
      <EditFormDialog mode={"collection"} edit={showForm} onClose={setShowForm} onUpdate={handleUpdateCollection}
        collection_id={folderId} folder_name={folderName} folder_desc={folderDesc}
      />
    </>
  );
}

function deleteCollection(id) {

	firebase.firestore()
		.collection("collections")
		.doc(id)
		.delete()
		.then(() =>
			console.log("Collection successfully deleted")
		).catch((err) =>
			console.error("Error removing document: ", err)
		)
}

function useDetails() {
	const [folderDetails, setFolderDetails] = useState([])

	useEffect(() => {
		const unsubscribe = firebase
			.firestore()
			.collection("collections")
			.onSnapshot((snapshot) => {
				const newDetails = snapshot.docs.map((doc) => ({
					folder_id: doc.id,
					...doc.data()
				}))
				setFolderDetails(newDetails)
			})
        
      return () => unsubscribe()
    }, [])
    
    return folderDetails
}

export default Collection