import { createMuiTheme } from '@material-ui/core/styles'

export const lightTheme = createMuiTheme({
  palette: {
		background: {
			default: "#dadada"
		},
    primary: {
      light: '#e0e0e0',
      main: '#37474f',
      dark: '#102027',
      contrastText: '#fff',
    },
    secondary: {
      light: '#ffffa6',
      main: '#dce775',
      dark: '#a8b545',
      contrastText: '#000',
    },
	},
	typography: {
		fontSize: 12,
		subtitle1: {
			fontWeight: 700,
			color: "#37474f"
		},
		h5: {
			fontWeight: 900,
			letterSpacing: "0.04rem"
		},
		h6: {
			fontWeight: 900,
			color: '#37474f',
			letterSpacing: "0.03rem",
			textTransform: "uppercase"
		}
	},
	overrides: {
		MuiBottomNavigationAction: {
			root: {
				color: "#62727b",
				"&$selected": {
					color: "#dce775"
				}
			}
		},
	}
})

export const darkTheme = createMuiTheme({
  palette: {
		background: {
      default: "#424242",
      paper: "#333"
		},
    primary: {
      light: '#484848',
      main: '#212121',
      dark: '#000000',
      contrastText: '#fff',
    },
    secondary: {
      light: '#ffffa6',
      main: '#dce775',
      dark: '#a8b545',
      contrastText: '#000',
    },
	},
	typography: {
		fontSize: 12,
		subtitle1: {
			fontWeight: 700,
			color: "#fff"
    },
    overline: {
      color: "#fff8"
    },
    caption: {
      color: "#fff8"
    },
		h5: {
			fontWeight: 900,
			letterSpacing: "0.04rem"
		},
		h6: {
			fontWeight: 900,
			color: '#fff',
			letterSpacing: "0.03rem",
			textTransform: "uppercase"
		}
	},
	overrides: {
		MuiBottomNavigationAction: {
			root: {
				color: "#616161",
				"&$selected": {
					color: "#dce775"
				}
			}
    },
    MuiIconButton: {
      colorPrimary: {
        color: "#fff"
      }
    },
    MuiListItemText: {
      secondary: {
        color: "#fff8"
      }
    },
    MuiOutlinedInput: {
      root: {
        color: "#fff",
        '& $notchedOutline': {
            borderColor: '#fff8',
        },
        '&$focused $notchedOutline': {
            borderColor: '#dce775',
            borderWidth: 2,
        },
      },
    },
    MuiFormLabel: {
      root: {
        color: "#fff8",
        '&$focused': {
            color: '#dce775'
        }
      }
    },
    MuiMenuItem: {
      root: {
        color:"#fff"
      }
    }
	}
})